
.formgroup{
  width: 60%;
  margin: 15px auto;
}
.form{
  width: 80%;
  height: 34px;
  padding: 6px 12px;
  line-height: 34px;
  color: #555;
  background-color: #fff;
  border: 1px solid #ccc;
  transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s
}
.lab{
  display: inline-block;
}
.text{width: 80%;
  height: 150px;
  padding: 6px 12px;
  line-height: 20px;
  vertical-align: top;
  color: #555;
  resize: none;
  background-color: #fff;
  border: 1px solid #ccc;
  transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s
}
.subbtn{
  float: right;
  margin-right: 28px;
}



