
.content {
  font-size: 12px;
  width: 600px;
  height: 600px;
  margin:50px auto;
  padding: 0px;
  background: #eee;
  overflow:hidden;
}
.menu{
	width: 100px;
	height: 100%;
	float: left;
	padding-left: 0px;
	text-align: center;
	background: #222d32;
}
.tabs{
	height: 40px;
	line-height: 40px;
	cursor: pointer;
	color: white;
}
.tabs:hover{
	background: black;
}
.tabs.active{
	background: black;
}
.tab{
	width: 500px;
	height: 100%;
	float: left;
}
.tab-content{
	width: 100%;
	height: 100%;
	display: none;
}
.tab-content.active{
	display: block;
}



