/* Fix user-agent */
* {
  box-sizing: border-box;
}
html {
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
}
html, input {
  font-family:
    "HelveticaNeue-Light",
    "Helvetica Neue Light",
    "Helvetica Neue",
    Helvetica,
    Arial,
    "Lucida Grande",
    sans-serif;
}
h1{text-align:center;margin: 0;padding-top: 20px;
}
ul {
  list-style: none;
  word-wrap: break-word;
}
.page{
	position:relative;
	width: 500px;
	height: 558px;
	margin-top: 10px;
	border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
	background-color: #fff;
}
.log {
  color: gray;
  margin: 5px;
  text-align: center;
  overflow: hidden;
}

/* Messages */
.chatArea {
  height: 100%;
  padding-bottom: 60px;
}
.messages {
  height: 100%;
  margin: 0;
  padding: 10px 20px 10px 20px;
  overflow-y: auto;
}
.message{padding: 2px;overflow: hidden;
}
.username{margin-right: 6px;
}
.messageBody{padding-left: 10px;margin: 2px;
}

/* Input */
.inputMessage {
  border-top: 1px solid #ccc;
  border-left: none;
  border-right: none;
  border-bottom: none;
  text-indent: 10px;
  bottom: 0;
  height: 60px;
  left: 0;
  outline: none;
  position: absolute;
  right: 0;
  width: 100%;
}



